.bodyBackground{
    background-color: rgb(143, 119, 92);
    background-image: url(data:image/png;base64,); 
    /* background-image:linear-gradient(163deg, #4f60ac 0%, rgba(98, 70, 126, 0.8) 11.8%, rgba(176, 60, 196, 0.6) 23.6%, rgba(192, 55, 173, 0.4) 35.4%, rgba(217, 46, 116, 0) 59%), radial-gradient(100% 100% at 10% 100%, #618aac 0%, #63965c 14.29%, #65944f 28.57%, #8fa75f 42.86%, #58909e 57.14%, #a2525f 71.43%, #6d5b45 100%); */
}
button {
    margin: 5px;
    padding: 5px;
    height: 30px;
    min-width: 80px;
    border-radius: 12px;
    border: none;
    cursor: pointer;
    font-weight: 700;
}
button:active {
    font-weight: 300;
    height: 30px;
    border: rgb(20, 20, 20);
    border-width: 2px;
    border-style: solid;
}
.titleH1 {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    color:rgba(255, 255, 255, 0.8);
    border-radius: 4px;
    padding: 20px;
    background-image: url(data:image/png;base64,);
    background-color: rgba(0, 0, 0, 0.99);
    filter: drop-shadow( 0px 4px 0px rgba(0, 0, 0, 0.3));
}
.sidebarH2 , h2 {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: larger;
    font-weight: bolder;
    text-indent: 8px;
    color:rgba(0, 0, 0, 0.7);
}
.textspan{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align:justify;
    font-family: Arial, Helvetica, sans-serif;
    font-size: small;
    color:rgba(0, 0, 0, 0.7);

}
.mainClass{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.gridcontainer{
    display: grid;
    justify-items: center;
    grid-template-columns: 22% 80%;
}
.buttoncontainer{
    display: flex;
    justify-content: space-evenly;
}
.sidebar{
    width: 180px;
    max-height: max-content;
    margin: 10px;
    padding: 5px;   
    border-radius: 8px;
    background-image: url(data:image/png;base64,);
    background-color: aliceblue;
    filter: drop-shadow( 0px 4px 0px rgba(0, 0, 0, 0.3));
}
.divs{
    width: 200px;
    height: 200px;
    margin: 10px;
    padding: 5px;   
    border-radius: 8px;
    background-image: url(data:image/png;base64,);
    background-color: aliceblue;
    filter: drop-shadow( 0px 4px 0px rgba(0, 0, 0, 0.3));
}
.commentdiv{
    min-height: fit-content;
    max-height: max-content;
    max-width: inherit;
    margin: 10px;
    padding: 20px;  
    border-radius: 8px;
    background-image: url(data:image/png;base64,);
    background-color: aliceblue;
    filter: drop-shadow( 0px 4px 0px rgba(0, 0, 0, 0.3));
}
.svg1, .svg2, .svg3 {
    fill:rgb(0, 0, 0);
    transform-origin: center;
    transition: 200ms;
}
.svg1:hover {
    scale: 104%;
    transition: 500ms;
    fill: rgb(0, 38, 255);
    filter: drop-shadow( 0px 10px 4px rgba(0, 0, 0, 0.2));
}
.svg1:active {
    scale: 97%;
    transition: 50ms;
    transition-timing-function: ease-out;
    fill:rgb(90, 0, 0);
    filter: drop-shadow( 0px 2px 0px rgba(0, 0, 0, 0.4));
}
.svg2:hover {
    transition-delay: 100ms;
    fill: rgb(39, 53, 70);
    filter: drop-shadow( 0px 0px -4px rgba(255, 230, 0, 0.5));
}
.svg2:active {
    scale: 94%;
    transition: 0ms;
    fill:rgba(0, 0, 0, 0.8);
    filter: blur(6px);
}
.svg2special {
    display: none;
    scale: 100%;
    fill: rgba(0, 0, 0, 1);
}
.svg2special:hover {
    display: block;
    scale: 200%;
    transition: 400ms;
    fill: rgba(0, 0, 0, 0);
}
.svg3:hover {
    scale: 101%;
    stroke: aqua;
    transition: 300ms;
    fill: rgb(31, 73, 79);
    filter: drop-shadow( 0px 60px 6px rgba(0, 0, 0, 0.2));
}
.svg3:active {
    scale: 90%;
    transition: 200ms;
    transition-timing-function: ease-out;
    fill:rgb(0, 0, 0);
    filter: drop-shadow( 0px 2px 0px rgba(0, 0, 0, 0.6));
}